  background-color: rgba(255, 255, 255, 0.8);
}

p {
  margin-top: 0;
}

p:last-child {
  margin-bottom: 0;
}

table {
  border-collapse: collapse;
}

.title {
  text-transform: uppercase;
  line-height: 1.1;
  color: #2d3238;
}

.title__big {
  display: block;
  font-weight: 900;
  font-size: 2em;
}

.title__sub {
  display: block;
  font-weight: normal;
}

.title--section {
  font-size: 18px;
  margin-bottom: 2em;
}

.title--product {
  font-size: 18px;
  margin: 0 0 2em;
}

.title-small {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 22px;
  text-align: center;
  margin-bottom: 2em;
}

.title-small--color {
  color: #4c5b82;
}

.about__wrap {
  margin-left: 25%;
}

.people {
  text-align: center;
  font-size: 18px;
}

.people__photo {
  margin-bottom: 1em;
}

.people__name {
  font-weight: bold;
}

.about-company {
  border: 2px solid #c3c3c3;
  padding: 15px 0;
  border-width: 2px 0;
  display: flex;
}

.about-company__people {
  flex-shrink: 0;
  margin-right: 60px;
}

.about-company__txt {
  font-size: 20px;
  font-weight: bold;
  font-style: italic;
  line-height: 1.8;
  color: #494848;
  padding-left: 50px;
  background: url("../img/quote.svg") no-repeat top left;
}

.intro {
  position: relative;
  display: flex;
  min-height: 700px;
}

@media (min-width: 1200px) {
  .intro {
    background: url("../img/bg-intro-sec.jpg") no-repeat -40% 100%;
  }
}

.intro__title {
  font-size: 24px;
}

.intro__cnt-wrap {
  flex: 1;
}

.intro__cnt {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 60px;
  margin: 70px 0 auto 15%;
  position: relative;
  z-index: 1;
}

.intro__znak {
  position: absolute;
  right: 0;
  top: 0;
}

.intro__btn {
  margin-left: auto;
}

.intro__img {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
}

.intro__img img {
  max-width: none;
  height: 100%;
  object-fit: content;
}

.burger {
  position: absolute;
  cursor: pointer;
  user-select: none;
  display: none;
  top: 20px;
  z-index: 9980;
  width: 30px;
  min-height: 26px;
  position: relative;
  background-color: #fea724;
  padding: 30px;
  text-align: center;
  border-radius: 50%;
  top: 0;
}

.burger span {
  width: 30px;
  top: 50%;
  transform: translateY(-50%);
  transform-origin: 50% 50%;
}

.burger span, .burger span::before, .burger span::after {
  display: block;
  position: absolute;
  left: 0;
  height: 4px;
  background: #fff;
  transition: all 0.2s ease;
}

.burger span::before, .burger span::after {
  content: '';
  width: 100%;
}

.burger span::before {
  top: -11px;
}

.burger span::after {
  bottom: -11px;
}

.burger.toggle-open span {
  background: rgba(0, 0, 0, 0);
  transform: rotate(45deg);
}

.burger.toggle-open span::before {
  transform: translate3d(0, 11px, 0);
}

.burger.toggle-open span::after {
  transform: rotate(-90deg) translate3d(11px, 0, 0);
}

.burger span {
  left: 15px;
}

.menu__list {
  display: flex;
  flex-direction: column;
}

.menu__link {
  text-decoration: none;
}

.page {
  display: flex;
  width: 100%;
  min-height: 100vh;
  flex-direction: column;
}

.page__content {
  flex: 1 0 auto;
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 1400px;
  padding-left: 20px;
  padding-right: 20px;
}

.section {
  padding: 60px 0;
}

.spacer {
  margin-bottom: 90px;
}

.navbar {
  background-color: rgba(45, 50, 56, 0.9);
  padding: 20px 0;
  color: #fff;
}

.navbar__wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.navbar__logo {
  flex: 0 0 120px;
  margin-right: 15px;
}

@media (max-width: 750px) {
  .navbar__logo {
    flex: 0 0 90px;
  }
}

.navbar__slug {
  font-size: 12px;
  font-weight: bold;
}

@media (max-width: 750px) {
  .navbar__slug {
    display: none;
  }
}

.navbar__contact {
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.navbar__tel {
  margin-right: 20px;
}

@media (max-width: 540px) {
  .navbar__tel {
    display: none;
  }
}

@media (min-width: 750px) {
  .navbar__tel {
    font-size: 24px;
  }
}

.navbar__call {
  margin-left: 20px;
}

@media (max-width: 750px) {
  .navbar__call {
    display: none;
  }
}

@media (max-width: 750px) {
  .navbar__work-time {
    display: none;
  }
}

.product {
  margin-bottom: 90px;
}

.product:last-child {
  margin-bottom: 0;
}

.product__title {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

@media (min-width: 540px) {
  .product__title {
    flex-direction: row;
  }
}

.product__title-head {
  flex: 0 0 auto;
}

@media (min-width: 540px) {
  .product__title-head {
    margin-right: 15px;
    width: 33.33333%;
  }
}

.product__desc {
  font-size: 14px;
}

.product__img {
  margin-left: 15px;
  margin-right: 15px;
  flex: 0 0 auto;
  width: calc(33.33333% - 30px);
}

.product__items {
  margin-left: 15px;
  margin-right: 15px;
  flex: 0 0 auto;
  width: calc(66.66667% - 30px);
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 540px) {
  .prod-item {
    margin-bottom: 40px;
  }
}

@media (min-width: 540px) {
  .prod-item {
    flex: 0 1 50%;
  }
  .prod-item:nth-child(odd) {
    padding-right: 30px;
  }
}

.prod-item__header {
  font-size: 18px;
  margin: 0;
  background-color: #2d3238;
  color: #fff;
  font-weight: bold;
  padding: 1em;
  line-height: 1.2;
  min-height: 80px;
  display: inline-flex;
  align-items: center;
  width: 100%;
}

.prod-table {
  width: 100%;
}

.prod-table td, .prod-table th {
  text-align: center;
  vertical-align: bottom;
  padding: 5px 2px;
}

.prod-table td:first-child, .prod-table th:first-child {
  text-align: left;
}

.prod-table tr:nth-child(even) {
  background-color: #EAEAEA;
}

.prod-table td {
  border-bottom: 1px solid #B8B7B7;
}

.prod-table th {
  font-size: 12px;
  background-color: #C3D5DC;
}

.prod-table__name {
  font-size: 1.1em;
  font-weight: bold;
  color: #4c5b82;
}

.prod-table__price {
  color: #4c5b82;
  font-size: 22px;
  font-weight: 900;
}

.prod-table__price span {
  font-size: .7em;
}

.slick-arrow {
  display: block;
  font-size: 0;
  color: transparent;
  background-color: transparent;
  background-size: cover;
  border: none;
  padding: 0px;
  appearance: none;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  outline: none;
  cursor: pointer;
}

.slick-prev {
  left: 0;
  background: url("../img/arrow-prev.svg") no-repeat;
}

.slick-next {
  right: 0;
  background: url("../img/arrow-next.svg") no-repeat;
}

.sept-count .slick-arrow {
  border-radius: 50%;
  border: 2px solid #fea724;
  background: #FFD185;
}
